/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: "Space Grotesk", sans-serif;
}
@media (min-width: 999px) {
  body {
    flex-direction: row;
  }
}

.contenedor {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-self: center;
  width: 95%;
  margin: 0 auto;
  max-width: 35rem;
  padding: 10rem 0 0 0;
}
@media (min-width: 999px) {
  .contenedor {
    width: 40%;
    max-width: 140rem;
    margin-right: 12rem;
  }
}

.button {
  padding: 1.4rem;
  margin-top: 1rem;
  margin-bottom: 0;
  font-size: 1.8rem;
  border-radius: 1rem;
  border: none;
  color: hsl(270deg, 3%, 87%);
  background-color: hsl(278deg, 68%, 11%);
}
.button:hover {
  cursor: pointer;
  background-color: hsl(279deg, 6%, 55%);
  color: hsl(278deg, 68%, 11%);
}

.unshow {
  display: none;
}

.bg {
  background-image: url("../../images/bg-main-mobile.png");
  background-position: center center;
  background-size: cover;
  width: 100%;
  height: 30vh;
  position: relative;
  display: flex;
  flex-direction: column;
  align-self: flex-start;
  align-items: center;
  padding: 2rem;
}
@media (min-width: 999px) {
  .bg {
    background-image: url("../../images/bg-main-desktop.png");
    background-position: center center;
    background-size: cover;
    max-width: 40vh;
    height: 100vh;
    align-self: center;
    align-items: center;
    justify-content: center;
  }
}

h1, h2, h3, p {
  margin: 0;
}

a {
  text-decoration: none;
}

