.back-card {
  display: flex;
  align-self: center;
  justify-content: flex-end;
  background-image: url("../../images/bg-card-back.png");
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  border-radius: 0.8rem;
  padding: 2rem;
  min-width: 28rem;
  min-height: 16rem;
  margin-left: 6rem;
}
@media (min-width: 999px) {
  .back-card {
    left: 0;
    min-width: 32rem;
    min-height: 20rem;
    margin-left: 10rem;
    margin-top: 28rem;
  }
}
.back-card__cvc {
  color: hsl(0deg, 0%, 100%);
  font-size: 1.2rem;
  letter-spacing: 0.1rem;
  margin: 5.4rem 1rem 0 0;
}
@media (min-width: 999px) {
  .back-card__cvc {
    margin: 7.3rem 1.8rem 0 0;
  }
}

.front-card {
  display: flex;
  flex-direction: column;
  align-self: center;
  align-items: center;
  background-image: url("../../images/bg-card-front.png");
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  border-radius: 0.8rem;
  padding: 2rem;
  min-width: 28rem;
  min-height: 16rem;
  margin-top: 10.15rem;
  margin-right: 5rem;
}
@media (min-width: 999px) {
  .front-card {
    min-width: 32rem;
    min-height: 20rem;
    margin-left: 20rem;
    margin-bottom: 24rem;
  }
}
.front-card img {
  position: relative;
  align-self: flex-start;
  width: 22%;
  margin-top: 0.5rem;
}
@media (min-width: 999px) {
  .front-card img {
    margin-top: 1rem;
  }
}
.front-card p {
  color: hsl(0deg, 0%, 100%);
  font-size: 1.8rem;
}
.front-card__number {
  margin-top: 3.8rem;
  letter-spacing: 0.3rem;
}
@media (min-width: 999px) {
  .front-card__number {
    margin-top: 5rem;
  }
}
.front-card .name-exp {
  display: flex;
  gap: 12.4rem;
  margin-top: 1.6rem;
}
.front-card__name p, .front-card__exp p {
  font-size: 1rem;
  font-family: "Space Grotesk", sans-serif;
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 0.1rem;
}

.card-form {
  display: inline-flex;
  flex-direction: column;
  align-items: stretch;
}
@media (min-width: 999px) {
  .card-form {
    width: 80%;
  }
}
.card-form input {
  padding: 1.8rem 1.4rem 1.4rem 1.4rem;
  margin-bottom: 1.5rem;
  border-radius: 1rem;
  border: 1px solid hsl(270deg, 3%, 87%);
  -webkit-appearance: none;
}
.card-form input.error {
  border: 1px solid hsl(0deg, 100%, 66%);
}
.card-form input.error__text {
  margin: 0;
  color: hsl(0deg, 100%, 66%);
}
.card-form input::placeholder {
  font-size: 1.8rem;
  text-decoration: none;
  opacity: 0.5;
}
.card-form .exp-cvc {
  display: grid;
  column-gap: 1rem;
  margin-bottom: 1rem;
  grid-template-columns: 10rem 10rem 13rem;
  grid-template-rows: 2rem 4.4rem;
}
@media (min-width: 999px) {
  .card-form .exp-cvc {
    grid-template-columns: 12rem 12rem 2.5fr;
  }
}
.card-form .exp-cvc label:first-of-type {
  grid-column: 1/3;
}
.card-form .exp-cvc label:last-of-type {
  grid-column: 3/4;
  grid-row: 1/2;
}
.card-form .exp-cvc input {
  height: 100%;
}
@media (min-width: 999px) {
  .card-form .exp-cvc input {
    height: 100%;
    width: 100%;
  }
}
.card-form label {
  color: hsl(278deg, 68%, 11%);
  font-size: 1.2rem;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 0.6rem;
  letter-spacing: 0.3rem;
}
.card-form .button {
  margin-top: 2rem;
}

.confirmed {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.4rem;
  font-weight: 400;
}
.confirmed.contenedor {
  padding-top: 12rem;
}
@media (min-width: 999px) {
  .confirmed.contenedor {
    padding-top: 4rem;
  }
}
.confirmed img {
  margin: 0 auto 4rem auto;
  width: 8rem;
}
.confirmed h2 {
  font-size: 2.4rem;
  margin-bottom: 2rem;
}
.confirmed p {
  text-transform: none;
  color: hsl(279deg, 6%, 55%);
  font-size: 1.6rem;
  margin-bottom: 4rem;
}